

import wx from 'weixin-jsapi'
import $ from 'jquery'


export default {
  data: {
    title: ' ',
    desc: '2022AOO ',
    imgUrl: 'https://oss.sxyweb.com.cn/2022/q/sanofi0330/icons.jpg',
    link: "https://h5.lehuitech.com.cn/wj/22/05/aoo0512/#/home"
  },
  init: function (data) {
    var self = this;
    $.getJSON('/WXAuthorization/jsapi', {
      url: location.href.split('#')[0]
    }, function (result) {
      console.log(result)
      if (result) {
        var config = {
          debug: false, // ,apialertpclogpc
          appId: result.appId, // 
          timestamp: result.timestamp, // 
          nonceStr: result.nonceStr, // 
          signature: result.signature, // 1
          jsApiList: ['onMenuShareAppMessage','onMenuShareTimeline','updateAppMessageShareData', 'updateTimelineShareData'] // JSJS2
        };
        wx.config(config);
        self.share();
      }
    })
  },
  share: function () {
    var self = this;
    wx.ready(function () {
      var config = {
        link: self.data.link,
        imgUrl: self.data.imgUrl,
        title: self.data.title,
        desc: self.data.desc,
        success: function () { },
        cancel: function () {
          // 
        }
      };
      wx.onMenuShareAppMessage(config);
      wx.onMenuShareTimeline(config);
      // wx.updateAppMessageShareData(config);
      // wx.updateTimelineShareData(config);
    })
  },

};